
import { defineComponent } from 'vue';
import MegaMenu from './MegaMenu.vue';

export default defineComponent({
	components: { MegaMenu },
	name: 'NavbarMenu',
	setup() {},
	data() {
		return {
			onOff: false,
			isShow: false,
			listOne: false,
			listTwo: false,
			listThree: false,
			listFour: false,
		};
	},
	methods: {
		toggle() {
			console.log('is clicked', this.onOff);
			return (this.onOff = !this.onOff);
		},
	},
});
